<template>
  <div>
  <div id="logo_wrapper" @click="play()">
    <svg class="logo" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000">
      <g transform="scale(0.5) translate(500,500)" id="logo">
        <circle id="logo_circle" fill="#010202" cx="500" cy="500" r="495"></circle>
        <path id="logo_label" fill="#fff" d="M143.1 555.6h-23V434.8h23v120.8zM228.8 555.6H206V434.8l80.8 71.2v-71.2h22.8v120.8l-80.8-67.3v67.3zM420.1 508.90000000000003l36-74.1h25.1l-61 120.8-61-120.8h25.1l35.8 74.1zM529.3 555.6V434.8h23v120.8h-23zM836 434.8l58.8 120.7h-23.9l-8.6-18.9h-52.4l-8.6 18.9-24 .1L836 434.8zm-.1 43.8l-17.5 38.6h35.2l-17.7-38.6zM722.1 586.6h-25.7l-15.1-26.3c-2.5.3-5.1.5-7.8.5-34.7 0-62.9-28.2-62.9-62.9s28.2-62.9 62.9-62.9 62.9 28.2 62.9 62.9c0 24.5-14 45.7-34.5 56.1l20.2 32.6zm-48.6-129c-22.2 0-40.2 18.1-40.2 40.2 0 22.2 18.1 40.2 40.2 40.2 22.2 0 40.2-18.1 40.2-40.2s-18-40.2-40.2-40.2z"></path>
      </g>
    </svg>
    <h1 class="motto1"><span>DEMAND</span><span>MORE</span></h1>
    <h1 class="motto2"><span>CHANGE</span><span>NOW</span></h1>
    <h1 class="motto3"><span>KEEP</span><span>PACE</span></h1>
    <h1 class="motto4"><span>BUSINESS</span><span>FIRST</span></h1>
    <h2 class="gmbh"><span>G</span><span>M</span><span>B</span><span class="h">H</span></h2>
  </div>
  <div id="trigger"></div>
</div>
</template>

<script>
import ScrollMagic from 'ScrollMagic';
import gsap from 'gsap/TweenMax';

require('animation.gsap')
require('debug.addIndicators')

gsap.CSSPlugin.defaultSmoothOrigin = true;

export default {
  name: 'HelloWorld',
  data () {
    return {
      colors: ["#19A69C","#EA168C","#FBB831","#77308D"]
    }
  },
  computed: {
    timeline() {
      const
        wrapper = document.getElementById('logo_wrapper'),
        logo = document.getElementById('logo'),
        circle = document.getElementById('logo_circle'),
        label = document.getElementById('logo_label'),
        tl = new gsap.TimelineMax();

        tl
        .add('start')
        .to(wrapper, 3,
          {backgroundColor: "#000", ease: gsap.Power2.easeOutIn}
        )
        .fromTo(label, 3,
          {fill: "#000", rotation:0, scale: 0, transformOrigin:"50% 50%"},
          {fill: "#fff", rotation:360, scale: 1, transformOrigin:"50% 50%", ease: gsap.Power2.easeInOut},
          'start'
        )
        .to(logo, 3, {scale: 1, transformOrigin:"50% 50%", ease: gsap.Power2.easeInOut}, '-=3');
        
        
        this.colors.map((color, i) => {

          tl
          .add(`color${i+1}`)
          .to(label, 0.5, {scale: 1.25, fill: this.colors[i], yoyo: true, repeat: 1, ease: gsap.Back.easeIn})
          .fromTo(circle, 2,
            {fill: "transparent", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
            {fill: color, scale: 1, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power4.easeIn},
            '-=2'
          )

          .to(wrapper, 1, {backgroundColor: color, ease: gsap.Power4.easeIn}, '-=0.5')
          .staggerFromTo(
            Array.from(document.querySelectorAll(`h1.motto${i+1} span`)), 2,
            {left: '50vh', autoAlpha: 0},
            {left:0, autoAlpha: 1, ease: gsap.Power2.easeInOut},
            0.75,
            `color${i+1}+2`
          );
          if (i>0) {
            tl.to(
              Array.from(document.querySelectorAll(`h1.motto${i} span`)), 2,
              {autoAlpha: 0, ease: gsap.Power4.easeIn},
              `color${i+1}`
            )
          }
        })

        tl
        .fromTo(circle, 3,
          {fill: "#000", autoAlpha: 0, scale: 0, transformOrigin: "50% 50%"},
          {fill: "#000", scale: 1, autoAlpha: 1, transformOrigin: "50% 50%", ease: gsap.Power2.easeInOut}
        )
        .to(wrapper, 3,
          {backgroundColor: "#fff", ease: gsap.Power2.easeInOut},
          '-=1.5'
        )
        .to(
          Array.from(document.querySelectorAll(`h1.motto${this.colors.length} span`)), 2,
          {autoAlpha: 0, ease: gsap.Power4.easeIn},
          '-=3'
        )
        .staggerTo(
          Array.from(document.querySelectorAll(`h2.gmbh span`)), 1,
          {top: () => Math.random() * 50 - 25, rotation: () => Math.random() * 60 - 30, ease: gsap.Bounce.easeOut},
          0.2,
          '+=2'
        )
        // .staggerTo(
        //   Array.from(document.querySelectorAll(`h2.gmbh span`)), 1,
        //   {autoAlpha: 0, left: () => Math.random() * 1000 + 500, ease: gsap.Power3.easeOut},
        //   0.2,
        //   '+=2'
        // )
        // .to(
        //   wrapper, 3,
        //   {width: 50, height: 50, ease: gsap.Power4.easeIn}
        // );

        return tl;
    }
  },
  methods: {
    play() {
      this.timeline.play()
    }
  },

  mounted() {
    const
        trigger = document.getElementById('trigger');

      // tl.play();
      
    // const sm = new ScrollMagic.Controller();
    // sm.addScene(new ScrollMagic.Scene({
    //   triggerElement: trigger,
    //   duration: trigger.offsetHeight
    // }).setTween(this.timeline));
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1.motto1, h1.motto2, h1.motto3, h1.motto4 {
    font-size: 8vh;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
  }
  h1.motto1 span, h1.motto2 span, h1.motto3 span, h1.motto4 span {
    position: relative;
    margin: 0 1.5vh;
    opacity: 0;
  }

  h2.gmbh {
    font-size: 8vh;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
  }
  h2.gmbh span {
    top: calc(-60vh - 100px);
    position: relative;
    margin: 0 1.5vh;
    display: inline-block;
  }

  #trigger {
    height: 5000px;
  }
  #logo_wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 60px;
    background: #fff;
    /*background-image: radial-gradient(rgba(255,255,255,0.5), transparent);*/
    overflow: hidden;
  }
  .logo{
    width: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
</style>



